body {
  background-color: #e5e5e5;
  position: relative;
}

.nav {
  width: 100%;
  height: 40px;
  background-color: #3f19c6;
  align-items: center;
  gap: 50px;
  display: flex;
  position: fixed;
  top: 0;
}

.is-hidden {
  opacity: 0;
}

.list {
  flex-direction: column;
  gap: 20px;
  margin-left: 20vw;
  margin-right: 20vw;
  padding-left: 0;
  list-style-type: none;
  display: flex;
}

.list__item {
  border-bottom: 0;
  border-radius: 30px;
  padding: 5px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, .46);
}

.list__link {
  color: inherit;
  text-decoration: none;
}

.list__item img {
  width: 100%;
  object-fit: cover;
  border-radius: 30px;
  display: block;
}

